<template>
    <Button @click="select" class="view_demo"></Button>
</template>
<script>
import myPicker from 'my-picker'

export default {
    methods: {
        select(){
            (new myPicker({
                cols:{
                    labelKey: 'name',
                    valueKey: 'name',
                    options: [{name: ''},{name: ''},{name: ''}],
                }
            })).show()
        }
    }
}
</script>


