<template>
    <Doc>
        <P><Button class="view_demo" @click="show">pickerpicker</Button></P>
        <P><Button class="view_demo" @click="close">picker</Button></P>
    </Doc>
</template>
<script>
import myPicker from 'my-picker'

export default {
    methods: {
        show(){
            try{
                this.picker.show()
                setTimeout(()=>{
                    this.picker.hide()
                }, 2000)
            } catch(e){
                alert('picker')
            }
        },
        close(){
            try{
                this.picker.close()
                alert('picker')
            } catch(e){
                alert('picker')
            }
        },
    },
    created(){
        this.picker = new myPicker({
            cols: ['', '', '', '', '', '', '', ''],
        })
    }
}
</script>


