<template>
    <Button @click="select" class="view_demo"></Button>
</template>
<script>
import myPicker from 'my-picker'

export default {
    methods: {
        select(){
            (new myPicker({
                cols: {
                    prefix: '',
                    options: ['', '', '', '', ''],
                    suffix: '',
                }
            })).show()
        }
    }
}
</script>


